
/* ------------------------------------------------------------------- */
/* social links are show at the bottom of the screen for small screens */
/* for larger screens they are shown on the right hand side        */
/* ------------------------------------------------------------------- */

/* mobile view */
.social-links {
    position: sticky;
    bottom: 0;
    z-index: 4;
    background-color: var(--background-color);
    display:flex;
    flex-direction: row;
    gap: 12px;
    justify-content: right;
    align-items: center;
    padding:5px;
}
.social-links svg {
    height: 35px;
    padding: 7px;
    width: 35px;
    fill: var(--title-color);
    background-color: var(--background-color);
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
}
.social-links-title {
    display:none;
}
@media (min-width:760px) {
    .social-links.sidebar {
        display:flex;
        bottom: unset;
        height:0;
        flex-direction: column;
        margin-right: auto;
        margin-left: 40px;
        position: sticky;
        top: 67px;
        .social-links-title {
            display: inline-block;
            font-size: 1.6rem;
        }
    }
}
@media (min-width:1100px) {
    .social-links.sidebar {
        margin-left: 100px;
    }
}